
    import Play from 'vue-material-design-icons/Play.vue'
    import Video from 'vue-material-design-icons/Video.vue'

    export default {
        name:'VideoPlayer',
        components:{
            Play,
            Video
        },
        props:{
            src:String,
            format:{
                type: String,
                default: "video/mp4"
            }
        },
        data(){
            return{
                playing: false,
            }
        },
        methods:{
            play() {
                this.$refs.player.play();
                this.playing = true;
            },

            pause() {
                this.$refs.player.pause();
                this.playing = false;
            },
            togglePlay() {
                if (this.playing) {
                    this.pause();
                } else {
                    this.play();
                }
            },
        },
        computed:{
            videoSource(){
                return this.src!=""?this.src:""
            }
        }
    }
